:root {
  // base
  --font-family: "Ubuntu", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-black: #000;
  --color-light: #fff;
  --color-gray: #424551;
  --color-ebony: #1e212c;
  --color-storm: #787a80;
  --color-primary: #FF5A30;

  //sizes
  --grid-gap: 30px;
}