.hero{
    padding-top: 221px;
    padding-bottom: 353px;
    background-color: var(--color-gray);
    background-image: url('../img/hero-bg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    &__content{
        max-width: 590px;
    }
    &__btns{
        .btn{
            &:not(:last-child){
                margin-right: 22px;
            }
        }
    }
}