.portfolio-section {
    padding: 120px 0;
    background-color: var(--color-light);

    &__items {
        margin: 0 -30px -21px;
        padding: 0 30px 100px;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 48px;
    }

    &__slider-nav {
        transform: translateX(12px);
    }

    &__title {
        margin: 0;
        padding-bottom: 11px;
        max-width: 700px;
    }

}

.portfolio-item {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 4px;
    box-shadow:
        0px 80px 80px -20px rgba(154, 156, 165, 0.08),
        0px 30px 24px -10px rgba(154, 156, 165, 0.05),
        0px 12px 10px -6px rgba(154, 156, 165, 0.04),
        0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    background: var(--color-light);

    &__content {
        @include flex-column-center;
        padding: 0 10px;
        padding-top: 16px;
        padding-bottom: 24px;
    }

    &__title {
        margin: 0;
        margin-bottom: 5px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-ebony);
    }

    &__descr {
        font-size: 14px;
        line-height: 150%;
        color: var(--color-storm);
    }

    &__btn {
        display: none;
        margin: 0;
        margin-top: 24px;
        padding: 11px 31px;
        font-size: 14px;
        align-items: center;
        text-align: center;
    }
}