.video-block {
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    height: 500px;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 5;
        background-color: rgba(#1e212c, 0.6)
    }

    &__content {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__play {
        @include flex-all-center;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 10;
        border-radius: 100%;
        width: 80px;
        height: 80px;
        background-color: var(--color-primary);
        transform: translate(-50%, -50%);

        svg {
            width: 26px;
            height: 26px;
            fill: var(--color-light);
        }
    }

    &__mute {
        @include flex-all-center;
        position: absolute;
        left: 32px;
        bottom: 32px;
        z-index: 10;
        border-radius: 100%;
        width: 40px;
        height: 40px;
        background-color: rgba(#fff, 0.12);

        svg {
            width: 13px;
            height: 13px;
            fill: var(--color-light);
        }
    }
}