.services-section {
    padding-top: 212px;
    padding-bottom: 80px;
    background-color: var(--color-storm);
    background-image: url("../img/services-bg.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &__list {
        margin-bottom: 80px;
    }
}

.services-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--grid-gap);

}

.services-item {
    position: relative;
    display: block;
    padding: 89px 10px;
    border: 1px solid #e5e8ed;
    border-radius: 4px;
    box-shadow:
        0px 80px 80px -20px rgba(154, 156, 165, 0.08),
        0px 30px 24px -10px rgba(154, 156, 165, 0.05),
        0px 12px 10px -6px rgba(154, 156, 165, 0.04),
        0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    background: var(--color-light);

    &--construction {
        &::before {
            background-image: url("../img/services-construction.jpg");
        }
    }

    &--development {
        &::before {
            background-image: url("../img/services-development.jpg");
        }
    }

    &--design {
        &::before {
            background-image: url("../img/services-design.jpg");
        }
    }

    &--repairs {
        &::before {
            background-image: url("../img/services-repairs.jpg");
        }
    }

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 3;
        border-radius: inherit;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        background-color: var(--color-ebony);
        opacity: 0;
        transition: opacity .3s ease-in-out;
    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 5;
        border-radius: inherit;
        background-color: rgba(#1e212c, 0.6);
        opacity: 0;
        transition: opacity .3s ease-in-out;
    }

    &__content {
        @include flex-column-center;
        position: relative;
        z-index: 10;
    }

    &__icon {
        margin-bottom: 24px;
        fill: var(--color-primary);
        width: 52px;
        height: 52px;
        object-fit: cover;
        transition: fill .3s ease-in-out;
    }

    &__title {
        margin: 0;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-ebony);
        transition: opacity .3s ease-in-out;
    }

    &:hover {
        &::before {
            opacity: 1;
        }

        &::after {
            opacity: 1;
        }

        .services-item__icon {
            fill: var(--color-light);
        }

        .services-item__title {
            color: var(--color-light);
        }
    }
}